.recent-header-wrap {
  position: relative;
  overflow: hidden;
  padding: 10px;
}
.recent-title {
  padding-top: 10px;
  font-size: 4.9em;
  font-weight: bold;
  color: #8892b0;
  text-align: left;
  line-height: 0.9em;
  font-family: "SF Pro Text", -apple-system, system-ui, BlinkMacSystemFont,
    Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
  margin-bottom: 20px;
}
.recent-post-wrap {
  padding: 1em;
}
.each-recent-post {
  display: flex;
  background: rgba(0, 0, 0, 0.2);
  margin-bottom: 2em;
  border-radius: 10px;
  padding: 10px;
}
.each-recent-post {
  display: flex;
  flex-direction: column;
}
.post-img {
  height: 200px;
  width: 100%;
}
.post-img-wrap {
  max-height: 400px;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.each-recent-post .post-title {
  font-size: 1.4em;
  margin-bottom: 1em;
}

.each-recent-post .post-category {
  color: #8892b0;
  letter-spacing: 0.5px;
  font-size: 0.8em;
  margin: 10px 0 10px 0;
  overflow: hidden;
}

.each-recent-post .post-text {
  color: #8892b0;
  margin-bottom: 2em;
}
.each-recent-post .post-date {
  font-size: 0.8em;
  background: rgba(0, 0, 0, 0.2);
}
.post-container .recent-post-wrap:last-child {
  margin-bottom: 100px;
}

/* topic styles */
.all-topics-container {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  overflow: scroll;
  margin-bottom: 100px;
}
.topic-wrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  max-width: 120px;
  margin: 1em;
  justify-content: center;
  max-height: 200px;
}
.topic-img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
.topic-title {
  color: #8892b0;
  margin-top: 10px;
  font-size: 1.1em;
  letter-spacing: 0.5px;
  font-family: Calibre, "San Francisco", "SF Pro Text", -apple-system, system-ui,
    BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
}

@media (min-width: 641px) {
  /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
  .post-img {
    height: 300px;
    width: 100%;
  }
}
@media (min-width: 961px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
  .post-img-wrap {
    justify-content: center;
  }
  .post-img {
    height: 400px;
    width: auto;
  }
}
